$bgc-primary: rgba(20, 20, 10, 0.3); //|  
$bgc-additional: #858080; //|    ()
// $bgc-additional: #908070; //|    ()
$accent-color: #2f303a; //|   ()
$accent-color-alt: #313131; //| alt   ()
$button-color: rgba(0, 0, 0, 0.3); //| button   :
$backdrop-bgc: rgba(
  $color: #000000,
  $alpha: 0.2,
);

//,icons:
$icons-primary-color: #afb1b8;

//, text:
$primary-text-color: #212121;
$secondary-text-color: #CCC;
$text-title-color: #212121;
$text-button-color: #ffffff;

//,fonts:
$primary-font-family: sans-serif;
$title-font-family: sans-serif;

//,animation
$timing-function: cubic-bezier(0.4, 0, 0.2, 1); //.  
$time-amount: 250ms; //.  

//, breakpoints
$mobile: 480px; //.     
$tablet: 768px; //.    
$desktop: 1280px; //.    

